<template>
	<view>
		<wybar @clickLeft="back" :leftText="i18n.set"></wybar>
		<view class="wrap">
			<view v-for="(item,index) in array" :key="index" class="section row" @click="click(index)">
				<view class="title">{{item}}</view>
				<image src="../../../static/ads-seld-right.png" mode=""></image>
			</view>

		</view>
		<view class="quit" @click="logout">{{i18n.mine.loginOut}}</view>
	</view>
</template>

<script>
	import wybar from '../../../components/wybar/wyBar.vue'
	export default {
		components: {
			wybar
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		data() {
			return {
				version: '',
				cacheSize: 0,
				array: []
			}
		},
		onLoad() {
			this.version = getApp().globalData.version
			this.getcache()
			this.array = [this.i18n.mine.zhuxiaozhanghao]
		},
		methods: {
			back() {
				uni.navigateBack({

				})
			},
			click(index) {
				var that = this
				if (index == 0) {
					
					uni.showModal({
						title: '',
						content: that.i18n.mine.zhuxiaoTip,
						showCancel: true,
						cancelText: that.i18n.mine.zhuxiaoBtn2,
						confirmText: that.i18n.mine.zhuxiaoBtn1,
						confirmColor: '#007AFF',
						cancelColor: '#FC494A',
						success: res => {
							if (res.confirm) {
								that.requestDelate()
							}
						},
						fail: () => {},
						complete: () => {}
					});
				}


			},
			getcache() {},
			clearCache() {
				if (this.cacheSize == 0) {
					return
				}
				uni.showModal({
					title: '',
					content: '确定清理缓存吗？',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确认',
					confirmColor: '#7900B2',
					cancelColor: '#000',
					success: res => {
						if (res.confirm) {
							var that = this
							uni.showLoading({
								title: '清理中...',
								mask: false
							});
							setTimeout(() => {
								uni.hideLoading()
								that.cacheSize = 0
								uni.clearStorage()
								uni.showToast({
									title: '已清理缓存',
									icon: 'none'
								});
							}, 1000)
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			requestDelate() {
				uni.clearStorageSync();
				getApp().globalData.userinfo = ''
				uni.showModal({
					title: that.i18n.mine.zhuxiaoSucess,
					content: '',
					showCancel: true,
					cancelText: that.i18n.mine.zhidaole,
					// confirmColor: '#7900B2',
					// cancelColor: '#206CFE',
					success: res => {
						uni.reLaunch({
							url: '../../../pages/index/index'
						})
					},
					fail: () => {},
					complete: () => {}
				});
			},
			logout() {
				var that = this
				uni.showModal({
					title: that.i18n.mine.logoutTip,
					content: '',
					showCancel: true,
					cancelText: that.i18n.cancel,
					confirmText: that.i18n.sure,
					// confirmColor: '#7900B2',
					// cancelColor: '#206CFE',
					success: res => {
						if (res.confirm) {
							uni.clearStorageSync();
							getApp().globalData.userinfo = ''
							uni.reLaunch({
								url: '../../../pages/index/index'
							})
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	}
</script>

<style>
	@import url("./index.css");
</style>
